*:focus {
    outline: none;
}

@font-face {
    font-family: Lato;
    src: url('/fonts/Lato-Regular.ttf');
    font-weight: 400;
}

@font-face {
    font-family: Lato;
    src: url('/fonts/Lato-Semibold.ttf');
    font-weight: 600;
}

@font-face {
    font-family: Lato;
    src: url('/fonts/Lato-Bold.ttf');
    font-weight: 700;
}

:root {
    /* colors */
    --bgColor: #F4F5F6;
    --borderColor: #EBECED;
    --black: #434343;
    --dark: #636363;
    --violet: #c98bcf;
    --darkViolet: #9a9af2;
    --green: #82CA60;
    --blue: #66bee2;
    --white: #FFFFFF;
    --red: #E43E42;
    --darkGrey: #535353;
    --grey: #777777;
    --lighterGrey: #989898;
    --lightGrey: #adadad;
    --lightestGrey: #dcdee0;

    --transition: ease .3s;
    --animation: spin 1s linear infinite;
    --animationSlideIn: slideIn .5s ease .1s forwards;
    --animationSlideOut: slideOut .4s ease forwards;
    --animationSlideUp: slideUp .5s ease .1s forwards;
    --animationSlideDown: slideDown .4s ease forwards;
    --borderTransition: border-color var(--transition);
    --border: 1px solid var(--borderColor);
    --shadowShowMore: 0 -25px 100px 75px white;
}

a {
    color: inherit;
    text-decoration: inherit
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes slideIn {
    0% { -webkit-transform: translateX(100%); transform: translateX(100%); }
    100% { -webkit-transform: translateX(0); transform: translateX(0); }
}

@keyframes slideIn {
    0% { -webkit-transform: translateX(100%); transform: translateX(100%); }
    100% { -webkit-transform: translateX(0); transform: translateX(0); }
}

@-webkit-keyframes slideOut {
    0% { -webkit-transform: translateX(0); transform: translateX(0); }
    100% { -webkit-transform: translateX(100%); transform: translateX(100%); }
}

@keyframes slideOut {
    0% { -webkit-transform: translateX(0); transform: translateX(0); }
    100% { -webkit-transform: translateX(100%); transform: translateX(100%); }
}

@-webkit-keyframes slideUp {
  0% { -webkit-transform: translateY(100%); transform: translateY(100%); }
  100% { -webkit-transform: translateY(0); transform: translateY(0); }
}

@keyframes slideUp {
  0% { -webkit-transform: translateY(100%); transform: translateY(100%); }
  100% { -webkit-transform: translateY(0); transform: translateY(0); }
}

@-webkit-keyframes slideDown {
  0% { -webkit-transform: translateY(0); transform: translateY(0); }
  100% { -webkit-transform: translateY(100%); transform: translateY(100%); }
}

@keyframes slideDown {
  0% { -webkit-transform: translateY(0); transform: translateY(0); }
  100% { -webkit-transform: translateY(100%); transform: translateY(100%); }
}

* {
    word-break: break-word;
}

body {
    font-family: Lato;
    font-size: 14px;
    color: var(--black);
}

input, textarea {
    font-family: Lato;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus
input:-webkit-autofill, 
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0px 1000px #000 inset;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  -o-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

.tippy-tooltip {
    padding: 0.4375rem 0.875rem;
    font-size: 13px;
    line-height: 1;
    background-color: var(--dark);
}

.tippy-tooltip-content:first-letter {
    text-transform: uppercase;
}

.tippy-popper[x-placement^=right] [x-arrow].arrow-small {
    left: -4px !important;
    border-right-color: var(--dark);
}

.tippy-popper[x-placement^=left] [x-arrow].arrow-small {
    right: -4px !important;
    border-left-color: var(--dark);
}

.tippy-popper[x-placement^=top] [x-arrow].arrow-small {
    bottom: -4px !important;
    border-top-color: var(--dark);
}

.tippy-popper[x-placement^=bottom] [x-arrow].arrow-small {
    top: -4px !important;
    border-bottom-color: var(--dark);
}



